export default HireBlockchainDevelopers;
export const queryData = graphql`
  query HireBlockchainDevelopersPageQuery {
    HireBlockchainDevelopersImg: file(
      relativePath: { eq: "hire-blockchain-developers.png" }
    ) {
      childImageSharp {
        gatsbyImageData
      }
    }
    heroImage: file(
      relativePath: {
        eq: "hirescreens/devPages/blockchain-developers-hero.webp"
      }
    ) {
      childImageSharp {
        gatsbyImageData(
          layout: CONSTRAINED
          width: 1200
          quality: 90
          placeholder: BLURRED
        )
      }
    }

    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }

    allMetaJson(filter: { slug: { eq: "hire-blockchain-developers" } }) {
      nodes {
        metaTitle
        metaKeyword
        title
        slug
        description
      }
    }
  }
`;
